'use client';
import Link from 'next/link';

import SampleHeadphone from '../public/static/imgs/sample-headphone.jpg';
import SampleWatch from '../public/static/imgs/sample-watch.jpg';
import SampleSpeaker from '../public/static/imgs/sample-speaker.jpg';
import Image from 'next/image';

const products = [
  {
    id: 1,
    name: 'Wireless Headphones',
    description: 'Noise cancelling over-ear headphones with high fidelity sound.',
    price: 129.99,
    image: SampleHeadphone,
  },
  {
    id: 2,
    name: 'Smart Watch',
    description: 'Track your fitness and receive smart notifications on the go.',
    price: 89.99,
    image: SampleWatch,
  },
  {
    id: 3,
    name: 'Bluetooth Speaker',
    description: 'Portable speaker with deep bass and 12-hour battery life.',
    price: 59.99,
    image: SampleSpeaker,
  },
];

export default function ProductList() {
  return (
    <div className="max-w-7xl mx-auto py-12 px-4 sm:px-6 lg:px-8">
      <h2 className="text-4xl font-bold text-center mb-12">Our Products</h2>
      <div className="grid gap-10 grid-cols-1 sm:grid-cols-2 lg:grid-cols-3">
        {products.map((product) => (
          <div
            key={product.id}
            className="bg-white rounded-2xl shadow-md hover:shadow-xl transition-shadow duration-300 flex flex-col"
          >
            <Image 
              src={product.image}
              alt={product.name}
              width={500}
              height={300}
              className="rounded-t-2xl"
              placeholder="blur"
              blurDataURL={product.image.src}
              priority
              sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
              loading="lazy"
              quality={75}
              style={{ width: '100%', height: 'auto' }}
              onLoadingComplete={(img) => {
                const aspectRatio = img.naturalWidth / img.naturalHeight;
                img.style.width = '100%';
                img.style.height = `${img.naturalHeight / aspectRatio}px`;
              }}
              onError={(e) => {
                e.currentTarget.src = 'https://via.placeholder.com/500x300?text=Image+not+available';
                e.currentTarget.alt = 'Image not available';
              }}
              onLoad={(e) => {
                const img = e.currentTarget as HTMLImageElement;
                img.style.width = '100%';
                img.style.height = 'auto';
              }}
            />
            <div className="p-6 flex flex-col flex-grow">
              <h3 className="text-2xl font-semibold mb-2">{product.name}</h3>
              <p className="text-gray-500 text-sm mb-4">{product.description}</p>
              <div className="mt-auto flex items-center justify-between">
                <span className="text-xl font-bold">${product.price.toFixed(2)}</span>
                <Link href={`/buy/${product.id}`} passHref>
                  <button className="bg-blue-600 hover:bg-blue-700 text-white px-5 py-2 rounded-lg text-sm font-medium transition">
                    Buy Now
                  </button>
                </Link>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

